import Man from './img/pfp/man1.jpg'
import Lake from './img/feed/lake.jpg'
import './FeedItem.css'
function FeedItem() {
    return (
        <div className="feed-item">
        <div className="userpfp-and-username">
            <img className="pfp" src={Man} alt="e" />
            <h1>Sample name</h1>
        </div>
        <div className="feed-text">
            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ad ea dignissimos rem cum voluptatem id quas saepe doloribus totam delectus!</p>
        </div>
        <div className="feed-image">
            <img className="feed-image" src={Lake} alt="" />
        </div>
        </div>
    )
}

export default FeedItem;